body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: url("../images/space4.jpeg");
  background-size: contain;
  background-repeat: repeat;
  color: white;
  text-align: center;
}

/* label focus color */
 .sol-select input:focus + label {
   color: gray !important;
 }
 /* label underline focus color */
 .sol-select input:focus {
   border-bottom: 1px solid gray !important;
   -webkit-box-shadow: 0 1px 0 0 gray !important;
           box-shadow: 0 1px 0 0 gray !important
 }

.flex-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.inputs div {
  margin: 30px;
}

img {
  height: 400px;
  width: auto;
  margin: 1vw;
  -ms-flex-preferred-size: 20%;
      flex-basis: 20%;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 50px black;
          box-shadow: 0 0 50px black;
}

h1 {
  padding: 40px 0px;
  color: white;
